@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300&family=Poppins:wght@200;300&display=swap);

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

a {
  color: inherit;
  text-decoration: inherit;
}

.app {
  min-height: 98.5vh;
  border-radius: 15px;
  border-color: #0FCDC8;
  border-style: solid;
  border-width: 6px;
  padding: 5px;
}
.header {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.header-title {
    text-transform: uppercase;
    font-size: 6vh;
    font-family: 'Montserrat', sans-serif;
    color: #DE1E00;
    font-weight: bold;
}

.divider {
    width: 100%;
    margin: 10px;
    background-color: grey;
}
.content {
    display: flex;
    justify-content: space-around;
}

.settings {
    width: 45%;
    padding: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: 'Poppins', sans-serif;
}

.quiz-settings {
    font-size: 1.7rem;
    color: #DE1E00;
    font-weight: bold;
    margin-bottom: 1.4rem;
}

.banner {
    width: 55%;
    align-self: center;
    padding: 8px;
}

.settings-select {
    display: flex;
    flex-direction: column;
    padding: 20px;
    width: 100%;
    flex: 0.8 1;
    justify-content: space-evenly;
}

@media(max-width: 800px) {
    .content {
        flex-direction: column-reverse;
        align-items: center;
    }

    .banner {
        width: 85%;
        padding: 0;
        padding-top: 20px;
    }

    .settings {
        width: 100%;
    }
}
.error {
    width: 100%;
    padding: 10px;
    margin-bottom: 10px;
    border-radius: 4px;
    background-color: orangered;
    text-align: center;
    color: white;
    text-transform: capitalize;
}
.question {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.single-question {
    width: 95%;
    min-height: 350px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    border-radius: 15px;
    border-color: #0FCDC8;
    border-style: solid;
    border-width: 6px;
    padding: 20px;
    margin-top: 10px;
}

.options {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    flex: 1 1;
    align-items: center;
    justify-content: space-around;
    margin: 10px;
}

.single-option {
    width: 46%;
    height: 50px;
    padding: 15px 20px;
    border-radius: 10px;
    box-shadow: 3px 3px 2px rgba(0,0,0,0.3);
}

.right {
    background-color: rgb(7, 207, 0);
    color: white;
}

.wrong {
    background-color: rgb(233, 0, 0);
    color: white;
}

.controls {
    display: flex;
    width: 100%;
    justify-content: space-around;
}

@media (max-width: 614px) {
    .single-option {
        width: 100%;
    }
}
.quiz{
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: 'Poppins', sans-serif;
}

.subtitle {
    font-size: 25px;
    border-radius: 5px;
    border-style: solid;
    border-width: 1px;
    border-color: black;
    box-shadow: 4px 4px 2px rgba(0, 0, 0, 0.3);
    padding: 5px 10px;
}

.quiz-info {
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    text-transform: uppercase;
    margin: 10px;
}
.result {
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 60vh;
    text-align: center;
}

.title {
    font-size: 8vh;
    font-family: 'Poppins', sans-serif;
    color: #0079DE;
    font-weight: bold;
}
.footer {
    margin: 10px;
    font-size: 1.2rem;
    text-align: center;
    font-family: 'Montserrat', sans-serif;
}
