.content {
    display: flex;
    justify-content: space-around;
}

.settings {
    width: 45%;
    padding: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: 'Poppins', sans-serif;
}

.quiz-settings {
    font-size: 1.7rem;
    color: #DE1E00;
    font-weight: bold;
    margin-bottom: 1.4rem;
}

.banner {
    width: 55%;
    align-self: center;
    padding: 8px;
}

.settings-select {
    display: flex;
    flex-direction: column;
    padding: 20px;
    width: 100%;
    flex: 0.8;
    justify-content: space-evenly;
}

@media(max-width: 800px) {
    .content {
        flex-direction: column-reverse;
        align-items: center;
    }

    .banner {
        width: 85%;
        padding: 0;
        padding-top: 20px;
    }

    .settings {
        width: 100%;
    }
}